<template>
  <div class="flex xl:flex-row flex-col justify-between mb-8">
     <div class="lg:flex lg:divide-x lg:divide-grey-01">
         <div class="flex items-center lg:pr-4 lg:mb-0 mb-4">
           <router-link :to="{ name: 'settingCouponCodes' }" class="text-dark mr-4"><span class="material-icons">chevron_left</span></router-link>
           <h5>Edit Coupon Code</h5>
         </div>
         <div class="lg:pl-4 flex items-center">
           <Breadcrumb :items="breadcrumb">
             <span class="material-icons">chevron_right</span>
           </Breadcrumb>
         </div>
       </div>
   </div>
   <Card>
     <CouponCodeForm ref="refForm" v-model:data="formData" />
     <template #footer>
       <div class="flex justify-end">
         <Button color="teal" view="smooth" @click="updateCoupon" :loading="loading.update">Save</Button>
       </div>
     </template>
   </Card>
 </template>
 
 <script>
 import { defineComponent, ref, reactive } from 'vue'
 import { useRouter, useRoute } from 'vue-router'
 import { useCouponCodes } from 'composable/setting'
 import Card from 'bbUi/components/card/card.vue'
 import Input from 'bbUi/components/input/input.vue'
 import Button from 'bbUi/components/button/button.vue'
 import Pagination from 'components/Pagination.vue'
 import Breadcrumb from 'bbUi/components/breadcrumb/breadcrumb.vue'
 import CouponCodeForm from 'components/CouponCode/Form.vue'
 import { ElMessage } from 'element-plus'
 
 export default defineComponent({
   components: {
     Card,
     Input,
     Button,
     Breadcrumb,
     Pagination,
     CouponCodeForm
   },
   async setup(props, { emit }) {
     const { stateShow, formData, loading, actionShow, actionUpdate } = useCouponCodes()
     const router = useRouter()
     const route = useRoute()
     const refForm = ref(null)
     const breadcrumb = [{
       text: 'Settings',
       to: {
         name: 'setting'
       }
     }, {
       text: 'Coupon Codes',
       to: {
         name: 'settingCouponCodes'
       }
     },{
       text: 'Edit Coupon Code'
     }]
     const updateCoupon = async () => {
       const isValid = refForm.value?.validate && refForm.value.validate() || false
       if(isValid) await actionUpdate(route.params.id, () => {
         router.push({
           name: 'settingCouponCodes'
         })
         ElMessage({
           type: 'success',
           message: 'Coupon Code updated'
         })
       })
     }
     await actionShow(route.params.id)
     Object.assign(formData, {
       code: stateShow.value.code,
       type: stateShow.value.type,
       amount: stateShow.value.amount
     })
     return {
       stateShow,
       refForm,
       formData,
       loading,
       breadcrumb,
       updateCoupon
     }
   }
 })
 </script>
 